  -webkit-animation-timing-function: step-end(4);
          animation-timing-function: step-end(4);
}

@-webkit-keyframes animate_button{
  10%{
    background: #4a79ba;
  }
  30%{
    background: #cf9d6c;
  }
  50%{
    background: #D66EE0;
  }
  70%{
    background: #ecab54;
  }
  90%{
    background: #86BDC3;
  }
}

@keyframes animate_button{
  10%{
    background: #4a79ba;
  }
  30%{
    background: #cf9d6c;
  }
  50%{
    background: #D66EE0;
  }
  70%{
    background: #ecab54;
  }
  90%{
    background: #86BDC3;
  }
}
